@media only screen and (min-width: 700px){
    .container{
        padding-top:80px ;
    }
    .remove-fav{
        float: right;
        cursor: pointer;
        position: relative;
    }
    .remove-fav:hover{
        color: #c41515;
    }
    .wrap-box-fav{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: flex-start;
        margin: 0px 50px 0px 50px;
    }
    .wrap-pro-fav{
        margin: 15px;
        padding: 8px;
        max-height: 350px;
        max-width: 236px;
        margin-bottom: 25px;
        border: 1px solid #c41515;
        border-radius: 15px;
    }
    .pro-img{
        display: flex;
        justify-content: center;
        margin: 5px;
    }
    .pro-img img{
        border: 1px solid aliceblue;
        height: 200px;
    }
    .pro-des{
        color: darkslategray;
        max-height: 35px;
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        vertical-align: middle;
        text-overflow: ellipsis;
        width: 100%;
        overflow: hidden;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        margin-bottom: 0px !important;
    }
    .pro-des:hover{
        color: #8f2d2d;
    }
    .h-pro-price .orignal{
        color: gray;
        margin-right: 20px;
    }
    .h-pro-price .discounted{
        font-size: 24px;
    }
}
